
/**
 * Internal dependencies
 */
import { layer2s, layer3s } from './layers'

/**
 * External dependencies
 */
import classnames from 'classnames'

const SeparatorLayer = props => {
	const SeparatorComp = parseInt( props.layer, 10 ) === 2 ? layer2s[ props.design ] : layer3s[ props.design ]
	const mainClasses = classnames( [
		props.className,
		`ugb-separator__layer-${ props.layer }`,
	] )

	return (
		<SeparatorComp
			preserveAspectRatio="none"
			className={ mainClasses }
			aria-hidden="true"
		/>
	)
}

SeparatorLayer.defaultProps = {
	className: '',
	layer: 2,
	design: 'wave-1',
}

SeparatorLayer.Save = props => <SeparatorLayer { ...props } />

export default SeparatorLayer
