
/**
 * Internal dependencies
 */
import './custom-css'
import './deprecated'
import './style'

/**
 * External dependencies
 */
import {
	i18n, isPro, planName,
} from 'stackable'

/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'
import ImageDesignColored from './images/colored.png'
import ImageDesignColoredHover from './images/colored-hover.png'
import ImageDesignLineColored from './images/line-colored.png'
import ImageDesignLineColoredHover from './images/line-colored-hover.png'

if ( isPro && planName !== 'starter' ) {
	addFilter( 'stackable.accordion.edit.layouts', 'stackable/accordion', ret => {
		return [ ...ret,
			{
				label: __( 'Lined to Colored', i18n ), value: 'line-colored', image: ImageDesignLineColored, hoverImage: ImageDesignLineColoredHover,
			},
			{
				label: __( 'Colored', i18n ), value: 'colored', image: ImageDesignColored, hoverImage: ImageDesignColoredHover,
			},
		]
	} )
}

addFilter( 'stackable.accordion.itemclasses', 'stackable/accordion', ( classes, props ) => {
	const {
		design = 'basic',
		shadow = '',
	} = props.attributes

	return {
		...classes,
		[ `ugb--shadow-${ shadow || 3 }` ]: design === 'line-colored' || design === 'colored',
	}
} )
