
/**
 * Internal dependencies
 */
import './custom-css'
import './deprecated'
import './style'

import ImageDesignBordered from './images/bordered.png'
import ImageDesignOutlined from './images/outlined.png'
import ImageDesignLargeIcon from './images/large-icon.png'

/**
 * External dependencies
 */
import {
	i18n,
	isPro,
} from 'stackable'

/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'
import { __ } from '@wordpress/i18n'

if ( isPro ) {
	addFilter( 'stackable.notification.edit.layouts', 'stackable/notification', ret => {
		return [ ...ret,
			{
				label: __( 'Bordered', i18n ), value: 'bordered', image: ImageDesignBordered,
			},
			{
				label: __( 'Outlined', i18n ), value: 'outlined', image: ImageDesignOutlined,
			},
			{
				label: __( 'Large Icon', i18n ), value: 'large-icon', image: ImageDesignLargeIcon,
			},
		]
	} )
}

addFilter( 'stackable.notification.show', 'stackable/notification/show', ( options, blockProps ) => {
	const {
		design = 'basic',
	} = blockProps.attributes

	return {
		...options,
		columnBorder: design === 'outlined' || design === 'bordered',
		columnBackground: design !== 'plain' && design !== 'outlined',
		iconAlign: design !== 'large-icon',
		iconLocation: design === 'large-icon',
		iconSpacing: design === 'large-icon' ? false : options.iconSpacing,
	}
} )

addFilter( 'stackable.notification.setAttributes', 'stackable/notification/notifType/large-icon', attributes => {
	if ( attributes.design !== 'large-icon' ) {
		return attributes
	}

	return {
		...attributes,
		showIcon: true,
	}
} )

addFilter( 'stackable.notification.setAttributes', 'stackable/notification/notifType/large-icon', ( attributes, props ) => {
	if ( typeof attributes.design === 'undefined' ) {
		return attributes
	}

	if ( attributes.design !== 'large-icon' && props.attributes.design === 'large-icon' ) {
		return {
			...attributes,
			showIcon: false,
		}
	}
	return attributes
} )
