
/**
 * Internal dependencies
 */
import './custom-css'
import './deprecated'

/**
 * External dependencies
 */
import { i18n, isPro } from 'stackable'
import { __getValue, appendImportant } from '~stackable/util'

/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'
import ImageDesignCenterOverlay from './images/center-overlay.png'
import ImageDesignHalf from './images/half.png'
import ImageDesignHalfOverlay from './images/half-overlay.png'
import ImageDesignHuge from './images/huge.png'
import ImageDesignSideOverlay from './images/side-overlay.png'

if ( isPro ) {
	addFilter( 'stackable.header.edit.layouts', 'stackable/header', ret => {
		return [ ...ret,
			{
				label: __( 'Half Overlay', i18n ), value: 'half-overlay', image: ImageDesignHalfOverlay,
			},
			{
				label: __( 'Center Overlay', i18n ), value: 'center-overlay', image: ImageDesignCenterOverlay,
			},
			{
				label: __( 'Side Overlay', i18n ), value: 'side-overlay', image: ImageDesignSideOverlay,
			},
			{
				label: __( 'Half', i18n ), value: 'half', image: ImageDesignHalf,
			},
			{
				label: __( 'Huge', i18n ), value: 'huge', image: ImageDesignHuge,
			},
		]
	} )
}

// Special rules for: Advanced -> Column spacing -> Content Vertical Alignment
addFilter( 'stackable.header.advanced-column-spacing.vertical-align', 'stackable/header', ( styles, props ) => {
	const getValue = __getValue( props.attributes )

	const {
		design = 'basic',
		fullHeight = false,
	} = props.attributes

	if ( design === 'half-overlay' && fullHeight ) {
		return {
			'.ugb-content-wrapper': {
				justifyContent: appendImportant( getValue( 'columnContentVerticalAlign' ) ),
			},
			tablet: {
				'.ugb-content-wrapper': {
					justifyContent: appendImportant( getValue( 'tabletColumnContentVerticalAlign' ) ),
				},
			},
			mobile: {
				'.ugb-content-wrapper': {
					justifyContent: appendImportant( getValue( 'mobileColumnContentVerticalAlign' ) ),
				},
			},
		}
	}

	return styles
} )
