
/**
 * Internal dependencies
 */
import './custom-css'
import './deprecated'
import './style'
import ImageDesignCenteredQuote from './images/centered-quote.png'
import ImageDesignHighlight from './images/highlight.png'
import ImageDesignHude from './images/huge.png'

/**
 * External dependencies
 */
import { i18n, isPro } from 'stackable'

/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'

if ( isPro ) {
	addFilter( 'stackable.blockquote.edit.layouts', 'stackable/blockquote', ret => {
		return [ ...ret,
			{
				label: __( 'Centered Quote', i18n ), value: 'centered-quote', image: ImageDesignCenteredQuote,
			},
			{
				label: __( 'Huge', i18n ), value: 'huge', image: ImageDesignHude,
			},
			{
				label: __( 'Highlight', i18n ), value: 'highlight', image: ImageDesignHighlight,
			},
		]
	} )
}
