
/**
 * Internal dependencies
 */
import './custom-css'
import './style'
import './designs'
import ImageDesignHorizontalCard from './images/horizontal-card.png'
import ImageDesignImageCard from './images/image-card.png'
import ImageDesignPortfolio from './images/portfolio.png'
import ImageDesignPortfolio2 from './images/portfolio2.png'
import ImageDesignVerticalCard from './images/vertical-card.png'
import ImageDesignVerticalCard2 from './images/vertical-card2.png'

/**
 * External dependencies
 */
import { i18n, isPro } from 'stackable'
import { AdvancedRangeControl } from '~stackable/components'

/**
 * WordPress dependencies
 */
import {
	__, _x, sprintf,
} from '@wordpress/i18n'
import { TextControl } from '@wordpress/components'
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'

if ( isPro ) {
	addFilter( 'stackable.blog-posts.edit.layouts', 'stackable/blog-posts', ret => {
		return [ ...ret,
			{
				label: __( 'Portfolio', i18n ), value: 'portfolio', image: ImageDesignPortfolio,
			},
			{
				label: sprintf( _x( '%s %d', 'Nth Title', i18n ), __( 'Portfolio', i18n ), 2 ), value: 'portfolio2', image: ImageDesignPortfolio2,
			},
			{
				label: __( 'Vertical Card', i18n ), value: 'vertical-card', image: ImageDesignVerticalCard,
			},
			{
				label: __( 'Horizontal Card', i18n ), value: 'horizontal-card', image: ImageDesignHorizontalCard,
			},
			{
				label: sprintf( _x( '%s %d', 'Nth Title', i18n ), __( 'Vertical Card', i18n ), 2 ), value: 'vertical-card2', image: ImageDesignVerticalCard2,
			},
			{
				label: __( 'Image Card', i18n ), value: 'image-card', image: ImageDesignImageCard,
			},
		]
	} )

	addFilter( 'stackable.blog-posts.edit.inspector.style.posts.after', 'stackable/blog-posts', ( output, props ) => {
		const { setAttributes } = props
		const {
			postOffset = 0,
			postExclude = '',
			postInclude = '',
		} = props.attributes

		return <Fragment>
			<AdvancedRangeControl
				label={ __( 'Offset', i18n ) }
				min={ 0 }
				max={ 100 }
				allowReset={ true }
				value={ postOffset }
				onChange={ postOffset => setAttributes( { postOffset } ) }
				help={ __( 'Offset your posts by a specific number of items', i18n ) }
				placeholder="0"
			/>
			<TextControl
				label={ __( 'Exclude Post IDs', i18n ) }
				type="text"
				value={ postExclude }
				onChange={ postExclude => setAttributes( { postExclude } ) }
				help={ __( 'Excludes specific IDs from the display. Enter post IDs separated by a commas', i18n ) }
			/>
			<TextControl
				label={ __( 'Display Specific Post IDs', i18n ) }
				type="text"
				value={ postInclude }
				onChange={ postInclude => setAttributes( { postInclude } ) }
				help={ __( 'Limit display to only these specific IDs. Enter post IDs separated by a commas', i18n ) }
			/>
		</Fragment>
	} )
}

// Some layouts should apply paddings in an inner div.
addFilter( 'stackable.blog-posts.advanced-column-spacing.paddings.selector', 'stackable/blog-posts/column-paddings', ( selector, props ) => {
	const {
		design = 'basic',
	} = props.attributes

	if ( [ 'vertical-card', 'horizontal-card' ].includes( design ) ) {
		return '.ugb-blog-posts__content'
	}
	if ( design === 'image-card' ) {
		return '.ugb-blog-posts__header, .ugb-blog-posts__content'
	}

	return selector
} )

// Include custom post queries for our premium settings.
addFilter( 'stackable.blog-posts.postQuery', 'stackable/blog-posts/premium', ( postQuery, props ) => {
	const {
		postOffset = 0,
		postExclude = '',
		postInclude = '',
		postType = 'post',
		taxonomyType = '',
		taxonomy = '',
	} = props.attributes

	// Taxonomy for CPTs.
	const isCPT = postType !== 'post' && postType !== 'page'
	const taxonomyQuery = {}
	if ( isCPT && taxonomyType && taxonomy ) {
		taxonomyQuery[ taxonomyType ] = taxonomy
	}

	return {
		...postQuery,
		offset: postOffset,
		exclude: postExclude.split( ',' ).map( s => parseInt( s, 10 ) ).filter( i => ! isNaN( i ) ),
		include: postInclude.split( ',' ).map( s => parseInt( s, 10 ) ).filter( i => ! isNaN( i ) ),
		...taxonomyQuery,
	}
} )
