
/**
 * Internal dependencies
 */
import './custom-css'
import './deprecated'
import './style'

/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'

/**
 * External dependencies
 */
import {
	i18n, isPro, planName,
} from 'stackable'
import { __, sprintf } from '@wordpress/i18n'
import { Fragment } from '@wordpress/element'
import ImageDesignHalf from './images/half.png'
import ImageDesignOverlap from './images/overlap.png'
import ImageDesignOverlap2 from './images/overlap2.png'
import ImageDesignOverlap3 from './images/overlap3.png'
import ImageDesignOverlap4 from './images/overlap4.png'
import ImageDesignOverlap5 from './images/overlap5.png'
import ImageDesignOverlapShape from './images/overlap-shape.png'
import ImageDesignOverlapShape2 from './images/overlap-shape2.png'
import ImageDesignOverlapShape3 from './images/overlap-shape3.png'
import ImageDesignOverlapShape4 from './images/overlap-shape4.png'
import ImageDesignOverlapShape5 from './images/overlap-shape5.png'
import PanelEffectsSettings from '../../components/panel-effects-settings'

if ( isPro && planName !== 'starter' ) {
	addFilter( 'stackable.feature.edit.layouts', 'stackable/feature', ret => {
		return [ ...ret,
			{
				label: __( 'Half Background', i18n ), value: 'half', image: ImageDesignHalf,
			},
			{
				label: sprintf( __( 'Overlap Shape %s', i18n ), 1 ), value: 'overlap', image: ImageDesignOverlapShape,
			},
			{
				label: sprintf( __( 'Overlap Shape %s', i18n ), 2 ), value: 'overlap2', image: ImageDesignOverlapShape2,
			},
			{
				label: sprintf( __( 'Overlap Shape %s', i18n ), 3 ), value: 'overlap3', image: ImageDesignOverlapShape3,
			},
			{
				label: sprintf( __( 'Overlap Shape %s', i18n ), 4 ), value: 'overlap4', image: ImageDesignOverlapShape4,
			},
			{
				label: sprintf( __( 'Overlap Shape %s', i18n ), 5 ), value: 'overlap5', image: ImageDesignOverlapShape5,
			},
			{
				label: sprintf( __( 'Overlap Background %s', i18n ), 1 ), value: 'overlap-bg', image: ImageDesignOverlap,
			},
			{
				label: sprintf( __( 'Overlap Background %s', i18n ), 2 ), value: 'overlap-bg2', image: ImageDesignOverlap2,
			},
			{
				label: sprintf( __( 'Overlap Background %s', i18n ), 3 ), value: 'overlap-bg3', image: ImageDesignOverlap3,
			},
			{
				label: sprintf( __( 'Overlap Background %s', i18n ), 4 ), value: 'overlap-bg4', image: ImageDesignOverlap4,
			},
			{
				label: sprintf( __( 'Overlap Background %s', i18n ), 5 ), value: 'overlap-bg5', image: ImageDesignOverlap5,
			},
		]
	} )

	addFilter( 'stackable.feature.edit.inspector.style.before', 'stackable/feature', ( output, props ) => {
		const { setAttributes } = props
		const {
			design = 'plain',
			hoverEffect = '',
		} = props.attributes
		if ( design !== 'plain' ) {
			return (
				<Fragment>
					{ output }
					<PanelEffectsSettings
						hoverEffect={ hoverEffect }
						onChangeHoverEffect={ hoverEffect => setAttributes( { hoverEffect } ) }
						hoverEffectInclude={ design.match( /^overlap/ ) ? [ '', 'shadow' ] : [] }
					/>
				</Fragment>
			)
		}
		return output
	}, 11 )
}

addFilter( 'stackable.feature.itemclasses', 'stackable/feature', ( classes, blockProps ) => {
	const {
		design = 'plain',
		hoverEffect = '',
	} = blockProps.attributes

	if ( design === 'basic' || design === 'half' ) {
		return {
			...classes,
			[ `ugb--hover-${ hoverEffect }` ]: hoverEffect,
		}
	}

	return classes
} )

addFilter( 'stackable.feature.contentclasses', 'stackable/feature', ( classes, blockProps ) => {
	const {
		design = 'plain',
		hoverEffect = '',
	} = blockProps.attributes

	if ( design.match( /^overlap/ ) ) {
		return {
			...classes,
			[ `ugb--hover-${ hoverEffect }` ]: hoverEffect,
		}
	}

	return classes
} )

/**
 * When switching to a shape overlap layout, use the blob1 shape by default.
 */
addFilter( 'stackable.feature.setAttributes', 'stackable/feature/shape', ( attributes, blockProps ) => {
	if ( typeof attributes.design === 'undefined' ) {
		return attributes
	}

	const newDesign = attributes.design
	const oldDesign = blockProps.attributes.design
	if ( newDesign.match( /^overlap\d*$/ ) && ! oldDesign.match( /^overlap\d*$/ ) ) {
		attributes.imageShape = 'blob1'
	}
	if ( ! newDesign.match( /^overlap\d*$/ ) && oldDesign.match( /^overlap\d*$/ ) ) {
		attributes.imageShape = ''
	}

	return attributes
} )
