
/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'

/**
 * External dependencies
 */
import { isPro } from 'stackable'

/**
 * Internal dependencies
 */
import PanelCustomCSSSettings from '../../components/panel-custom-css-settings'

const mainClass = 'ugb-card'
const defaultCSS = `/* Card container */
.ugb-card {
\t
}

/* Card image */
.ugb-card .ugb-card__image-container {
\t
}

/* Card title */
.ugb-card .ugb-card__title {
\t
}

/* Card tagline */
.ugb-card .ugb-card__tagline {
\t
}

/* Card description */
.ugb-card .ugb-card__description {
\t
}

/* Card button */
.ugb-card .ugb-button {
\t
}

/* Card button text */
.ugb-card .ugb-button--inner {
\t
}`

if ( isPro ) {
	addFilter( 'stackable.card.edit.inspector.after', 'stackable/card', ( output, design, props ) => {
		const { setAttributes } = props
		const {
			customCSS = '',
			customCSSUniqueID = '',
		} = props.attributes

		return (
			<Fragment>
				{ output }
				<PanelCustomCSSSettings
					value={ customCSS }
					defaultCSS={ defaultCSS }
					customCSSUniqueID={ customCSSUniqueID }
					blockProps={ props }
					mainClass={ mainClass }
					onChangeCustomCSS={ customCSS => setAttributes( { customCSS } ) }
					onChangeCompiledCSS={ customCSSCompiled => setAttributes( { customCSSCompiled } ) }
					onChangeCSSUniqueID={ customCSSUniqueID => setAttributes( { customCSSUniqueID } ) }
				/>
			</Fragment>
		)
	}, 11 )
}
