/**
 * External dependencies
 */
import {
	i18n, isPro, planName,
} from 'stackable'
import { ColorPaletteControl, PanelAdvancedSettings } from '~stackable/components'

import { range } from 'lodash'

/**
 * WordPress dependencies
 */
import {
	__, _x, sprintf,
} from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'

if ( isPro && planName !== 'starter' ) {
	// Column background color.
	addFilter( 'stackable.testimonial.edit.inspector.style.general.after', 'stackable/testimonial', ( output, props ) => {
		const { setAttributes } = props
		const {
			design = 'basic',
		} = props.attributes
		return (
			<Fragment>
				{ output }
				{ ( design === 'vertical' || design === 'vertical-inverse' ) &&
					<PanelAdvancedSettings
						title={ __( 'Column Background', i18n ) }
						id="column-background"
						initialOpen={ false }
						className="ugb--help-tip-column-background-on-off"
					>
						<ColorPaletteControl
							value={ props.attributes.columnBackgroundColor || '' }
							onChange={ value => setAttributes( { columnBackgroundColor: value } ) }
							label={ __( 'Column Background', i18n ) }
						/>
					</PanelAdvancedSettings>
				}
			</Fragment>
		)
	} )

	addFilter( 'stackable.testimonial.edit.inspector.advanced.before', 'stackable/testimonial', ( output, props ) => {
		const { setAttributes } = props
		const {
			design = 'basic',
			columns = 3,
		} = props.attributes

		return (
			<Fragment>
				{ output }
				{ ( design === 'vertical' || design === 'vertical-inverse' ) && range( 1, columns + 1 ).map( i => (
					<PanelAdvancedSettings
						title={ sprintf( _x( '%s #%d', 'Panel title', i18n ), __( 'Column Background', i18n ), i ) }
						initialOpen={ false }
						key={ i }
					>
						<p className="components-base-control__help">{ sprintf( __( 'Override settings for column %d', i18n ), i ) }</p>
						<ColorPaletteControl
							value={ props.attributes[ `column${ i }BackgroundColor` ] || '' }
							onChange={ value => setAttributes( { [ `column${ i }BackgroundColor` ]: value } ) }
							label={ __( 'Column Background', i18n ) }
						/>
					</PanelAdvancedSettings>
				) ) }
			</Fragment>
		)
	}, 19 )
}
